import React, { useRef, useState, useCallback } from 'react';
import Webcam from 'react-webcam';
import Tesseract from 'tesseract.js';

const DateScanner = () => {
    const webcamRef = useRef(null);
    const [scannedDate, setScannedDate] = useState('');
    const [error, setError] = useState('');

    const capture = useCallback(() => {
        const imageSrc = webcamRef.current.getScreenshot();
        if (!imageSrc) {
            setError('Nie udao si uzyska obrazu z kamery.');
            return;
        }

        Tesseract.recognize(imageSrc, 'eng', {
            logger: (m) => console.log(m),
        })
            .then(({ data: { text } }) => {
                const datePattern = /\b(\d{2}\/\d{2}\/\d{4})\b/;
                const match = text.match(datePattern);
                if (match) {
                    setScannedDate(match[0]);
                    setError('');
                } else {
                    setError('Nie znaleziono daty.');
                }
            })
            .catch((error) => {
                console.error('Bd przy rozpoznawaniu tekstu:', error);
                setError('Bd przy rozpoznawaniu tekstu.');
            });
    }, [webcamRef]);

    return (
        <div>
            <Webcam
                audio={false}
                ref={webcamRef}
                screenshotFormat='image/jpeg'
                width={480}
                height={380}
                videoConstraints={{ facingMode: 'environment' }} // Ustawienie tylniej kamery
            />
            <button onClick={capture}>Skanuj dat</button>
            {error && <p style={{ color: 'red' }}>{error}</p>}
            {scannedDate && <p>Data wanoci: {scannedDate}</p>}
        </div>
    );
};

export default DateScanner;
