import { useScroll, useTransform, motion } from 'framer-motion';
import React, { useRef } from 'react';

function ZoomParallax() {
    const zoomParallaxContainer = useRef(null);

    const { scrollYProgress } = useScroll({
        target: zoomParallaxContainer,
        offset: ['start start', 'end end'],
    });

    const scaleName = useTransform(
        scrollYProgress,
        [0, 0.5, 0.9, 1],
        [0, 0, 0.8, 1.25]
    );
    const scale4 = useTransform(scrollYProgress, [0, 1], [1, 4]);
    const scale5 = useTransform(scrollYProgress, [0, 1], [1, 5]);
    const scale6 = useTransform(scrollYProgress, [0, 1], [1, 6]);
    const scale8 = useTransform(scrollYProgress, [0, 1], [1, 8]);

    const pictures = [
        {
            src: `${process.env.PUBLIC_URL}/parallaxImage.jpg`,
            scale: scale4,
            additionalText: true,
        },
        {
            src: 'https://images.unsplash.com/photo-1529720317453-c8da503f2051?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D',
            scale: scale5,
        },
        {
            src: 'https://images.unsplash.com/photo-1613820705337-7ac6cf0083d9?q=80&w=1974&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D',
            scale: scale6,
        },
        {
            src: 'https://images.unsplash.com/photo-1602810316693-3667c854239a?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D',
            scale: scale5,
        },
        {
            src: 'https://images.unsplash.com/photo-1587143770785-2293bcc6a754?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D',
            scale: scale6,
        },
        {
            src: 'https://images.unsplash.com/photo-1612029266697-15ba0130eae8?q=80&w=2070&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D',
            scale: scale8,
        },
        {
            src: 'https://images.unsplash.com/photo-1617896840444-04fe57adbe2d?q=80&w=2048&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D',
            scale: scale8,
        },
        {
            src: `${process.env.PUBLIC_URL}/photo.jpg`,
            scale: scale5,
        },
    ];
    return (
        <section
            ref={zoomParallaxContainer}
            className='zoom-parallax-container'
        >
            <div className='sticky-div'>
                {pictures.map(({ src, scale, additionalText }, index) => (
                    <motion.div
                        style={{ scale: scale }}
                        className='wrapper'
                        key={index}
                    >
                        <div className='img-container'>
                            {additionalText ? (
                                <div className='name-wrapper'>
                                    <motion.div
                                        style={{ scale: scaleName }}
                                        className='brand-name'
                                    >
                                        MUSTHAVE
                                    </motion.div>
                                </div>
                            ) : null}
                            <img src={src} alt='xd' placeholder='blur' fill />
                        </div>
                    </motion.div>
                ))}
            </div>
        </section>
    );
}

export default ZoomParallax;
