import { useScroll, useTransform, motion } from 'framer-motion';
import { useRef } from 'react';

export default function Section() {
    const container = useRef();
    const { scrollYProgress } = useScroll({
        target: container,
        offset: ['start end', 'end start'],
    });

    const y = useTransform(scrollYProgress, [0, 1], ['-10vh', '10vh']);

    return (
        <div
            ref={container}
            className='parallax-section'
            style={{ clipPath: 'polygon(0% 0, 100% 0%, 100% 100%, 0 100%)' }}
        >
            <div className='parallax-section_text'>
                <p>
                    BEAUTY AND QUALITY NEED THE RIGHT TIME TO BE CONCEIVED AND
                    REALISED EVEN IN A WORLD THAT IS IN TOO MUCH OF A HURRY.
                </p>
                <p>MUSTHAVE</p>
            </div>
            <div className='img-container'>
                <motion.div style={{ y }} className='img-wrapper'>
                    <img
                        src={`${process.env.PUBLIC_URL}/photo2.jpg`}
                        fill
                        alt='imxddage'
                    />
                </motion.div>
            </div>
        </div>
    );
}
